export default {
	onLoad() {
	},
	data() {
		return {
			tel: '',
			telInfo: {
				'telNumber':'',
				'city':'',
				'province':'',
				'catName':'',
			},
			show: false
		};
	},
	methods: {
		setIphone(e) {
			this.tel = e.detail.value;
		},
		telQuery() {
			uniCloud.callFunction({
			    name: 'telQuery',
				data:{
					"tel":this.tel
				}
			  })
			  .then(res => {
					console.log(res.result)
					let result = res.result
					if(result){
						this.show=true
						this.telInfo = {
							telNumber: this.tel,
							city:result.city,
							province: result.province,
							catName: result.type,
						};
					}
					
			  })
			  .catch(err=>{
				  uni.showToast({
				  	title:'手机号码错误'
				  })
				  console.log('fail');
			  })
		}
	}
};
